<template>
  <crud
    :prefix="prefix"
    :path="path"
    :pageTitle="pageTitle"
    :fieldsInfo="fieldsInfo"
    :detailsTitle="$t('detailsTitle')"
    :itemElements="itemElements"
  >
  </crud>
</template>

<script>
  import Crud from '@/crud/components/Crud.vue'

  export default {
    data() {
      return {
        prefix: 'admin',
        path: 'permissions',
        pageTitle: 'admin.permissions',
      }
    },
    computed: {
      itemElements() {
        return {
          permissionUsers: {
            title: this.$t('itemElements.permissionUsers.title'),
            url: 'admin/permissions/{id}/users',
            controller: 'admin/user-permissions',
            itemObject: 'user_permissions',
            columns: [
              {
                obj: 'name',
                name: 'name',
                header: this.$t('itemElements.permissionUsers.headers.user'),
              },
            ],
            primaryId: 'permission_id',
            foreignId: 'user_id',
            icon: 'people',
            color: 'purple',
            buttonText: this.$t('itemElements.permissionUsers.title')
          },
        }
      },
      fieldsInfo () {
        return [
          {
            text: this.$t('fields.id'),
            name: 'id',
            details: false,
          },
          {
            type: 'input',
            column: 'name',
            text: this.$t('fields.name'),
            name: 'name'
          },
          {
            type: 'input',
            column: 'code',
            text: this.$t('fields.code'),
            name: 'code'
          },
        ]
      },
    },
    components: {
      Crud
    },
    i18n: {
      messages: {
        pl: {
          detailsTitle: 'Uprawnienie',
          fields: {
            id: 'Id',
            name: 'Nazwa',
            code: 'Kod',
          },
          itemElements: {
            permissionUsers: {
              title: 'Uytkownicy posiadajcy dane uprawnienie',
              headers: {
                user: 'Uytkownik',
              }
            }
          },
        },
        en: {
          detailsTitle: 'Permission',
          fields: {
            id: 'Id',
            name: 'Name',
            code: 'Code',
          },
          itemElements: {
            permissionUsers: {
              title: 'Users with this permission',
              headers: {
                user: 'User',
              }
            }
          },
        }
      }
    },
  }

</script>



